<script>
import {
  deleteProduct,
  getProductDataAll,
  searchProductData,
} from "api/products";
import PageHeader from "components/common/menu/PageHeader";
import ProductForm from "views/master/ProductForm";
import { Message } from "element-ui";

export default {
  name: "ProductList",
  components: {
    PageHeader,
    ProductForm,
  },
  data() {
    return {
      ruleForm: {
        product_name: "",
        category1: "",
        price: "",
        CreateDate: "",
        Is_Delete: false,
      },
      product_name: "",
      category1: "",
      price: "",
      dialogFormVisible: false,
      loading: false,
      tableData: [],
      search: "",
    };
  },
  created: function () {
    this.getProductDataAll();
  },
  methods: {
    getProductDataAll() {
      this.loading = true;
      getProductDataAll()
        .then((res) => {
          if (res.data.status === 1) {
            this.tableData = res.data.data;
            this.loading = false;
          } else {
            this.tableData = [];
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    searchProductData() {
      let values = {};
      values["product_name"] = this.ruleForm.product_name;
      values["category1"] = this.ruleForm.category1;
      values["price"] = this.ruleForm.price;
      searchProductData(values)
        .then((res) => {
          this.tableData = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchProductData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    closeDialog() {
      this.dialogFormVisible = false;
    },
    reloadList() {
      this.dialogFormVisible = false;
      this.getProductDataAll();
    },
    handleEdit(index, row) {
      this.id = row.id;
      this.dialogFormVisible = true;
    },
    resetForm(formName) {
      console.log("resetForm");
      this.$refs[formName].resetFields();
    },
    handleDelete(index, row) {
      let values = {};
      values["id"] = row.id;
      deleteProduct(values)
        .then((res) => {
          if (res.status === 200) {
            this.getProductDataAll();
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
          } else {
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    createBtn() {
      this.id = "";
      this.dialogFormVisible = true;
    },
  },
};
</script>

